___CSS_LOADER_EXPORT___.push([module.id, "\n  /*You should put other CSS properties with same class names in the parent component's style tag that is not scoped.*/\n.custom-select[data-v-6539148d] {\n    position: relative;\n    width: 100%;\n    text-align: left;\n    outline: none;\n}\n.custom-select .selected[data-v-6539148d] {\n\n    cursor: pointer;\n    user-select: none;\n}\n.custom-select .selected i[data-v-6539148d] {\n    position: absolute;\n    right: 10px;\n    float: right;\n}\n.custom-select .items[data-v-6539148d] {\n    position: absolute;\n    left: 0;\n    right: 0;\n    z-index: 1;\n}\n.custom-select .items .option[data-v-6539148d] {\n    cursor: pointer;\n    user-select: none;\n}\n.selectHide[data-v-6539148d] {\n    display: none;\n}\n.button-div[data-v-6539148d] {\n    justify-content: center;\n    display: grid;\n    padding: 13px 0 19px;\n    margin-top: 10px\n}\n.button[data-v-6539148d] {\n    cursor: pointer;\n}\n.custom-placeholder[data-v-6539148d]:empty:before {\n    content:attr(data-placeholder);\n    color:#616161\n}\n.custom-placeholder[data-v-6539148d]:focus-visible {\n    outline: none;\n}\n  ", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/customSelect.vue"],"names":[],"mappings":";EA6FE,oHAAoH;AACpH;IACE,kBAAkB;IAClB,WAAW;IACX,gBAAgB;IAChB,aAAa;AACf;AAEA;;IAEE,eAAe;IACf,iBAAiB;AACnB;AAEA;IACE,kBAAkB;IAClB,WAAW;IACX,YAAY;AACd;AAEA;IACE,kBAAkB;IAClB,OAAO;IACP,QAAQ;IACR,UAAU;AACZ;AAEA;IACE,eAAe;IACf,iBAAiB;AACnB;AAEA;IACE,aAAa;AACf;AACA;IACE,uBAAuB;IACvB,aAAa;IACb,oBAAoB;IACpB;AACF;AACA;IACE,eAAe;AACjB;AACA;IACE,8BAA8B;IAC9B;AACF;AACA;IACE,aAAa;AACf","sourcesContent":["<template>\n  <div class=\"custom-select\" :tabindex=\"tabindex\" @blur=\"open = false\">\n    <div :class=\"open ? 'open selected ' + selectedClass : 'selected ' + selectedClass\" @click=\"open = !open\">\n      <div v-if = \"selected\">{{ typeof selected === 'object' ? selected.name : selected }}\n        <i class=\"mdi mdi-chevron-down\"></i>\n      </div>\n      <div v-else>\n        <span contenteditable=\"true\" :data-placeholder=\"placeholder\" class=\"custom-placeholder\"></span>\n        <i class=\"mdi mdi-chevron-down\"></i>\n      </div>\n    </div>\n    <div class=\"items\" :class=\"{ selectHide: !open }\" :style=\"{padding: extraButton ? '10px 0 0' : '10px 0'}\">\n      <div v-for=\"(option, i) of options\" :key=\"i\" @click=\"handleClick(option)\" class=\"option\">\n        {{typeof option === 'object' ? option.name : option }}\n      </div>\n      <div v-if=\"extraButton\" class=\"button-div\" :style=\"{borderTop: extraButton ? '1px solid #EEEEEE' :  'none'}\" >\n        <div v-for=\"(button, index) in buttons\" :key=\"index\">\n            <div :id=\"'option-button-' + index\" :class=\"button.buttonClass + ' button'\" @click=\"open=false\">{{ button.buttonText }}</div>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script setup>\nimport { ref } from 'vue'\nconst emit = defineEmits([\"input\"]);\nconst props = defineProps({\n  options: {\n    type: Array,\n    required: true,\n  },\n  default: {\n    type: String,\n    required: false,\n    default: '',\n  },\n  tabindex: {\n    type: Number,\n    required: false,\n    default: 0,\n  },\n  placeholder: {\n    type: String,\n    required: false,\n    default: \"\",\n  },\n  selectedClass: {\n    type: String,\n    required: true,\n    default: \"\",\n  },\n  extraButton: {\n    type: Boolean,\n    required: false,\n    default: false,\n  },\n  buttons: {\n    type: Array,\n    required: false,\n    default: () => [],\n    validator: (value) => {\n      return value.every(\n        (button) =>\n          typeof button === \"object\" &&\n          typeof button.buttonText === \"string\" &&\n          typeof button.buttonClass === \"string\"\n      );\n    },\n  },\n})\n\nconst selected = ref(props.default ? props.default : null);\nconst open = ref(false)\n\nconst handleClick = (option) => {\n    if(typeof option === 'object'){\n      emit('input', option.id);\n      selected.value = option;\n      open.value = false;\n      emit('input', selected.value.id);\n    }\n    else if(typeof option === 'string') {\n      emit('input', option);\n      selected.value = option;\n      open.value = false;\n      emit('input', selected.value);\n    }\n}\n</script>\n\n  \n  <style scoped>\n  /*You should put other CSS properties with same class names in the parent component's style tag that is not scoped.*/\n  .custom-select {\n    position: relative;\n    width: 100%;\n    text-align: left;\n    outline: none;\n  }\n  \n  .custom-select .selected {\n\n    cursor: pointer;\n    user-select: none;\n  }\n  \n  .custom-select .selected i {\n    position: absolute;\n    right: 10px;\n    float: right;\n  }\n  \n  .custom-select .items {\n    position: absolute;\n    left: 0;\n    right: 0;\n    z-index: 1;\n  }\n  \n  .custom-select .items .option {\n    cursor: pointer;\n    user-select: none;\n  }\n  \n  .selectHide {\n    display: none;\n  }\n  .button-div {\n    justify-content: center;\n    display: grid;\n    padding: 13px 0 19px;\n    margin-top: 10px\n  }\n  .button {\n    cursor: pointer;\n  }\n  .custom-placeholder:empty:before {\n    content:attr(data-placeholder);\n    color:#616161\n  }\n  .custom-placeholder:focus-visible {\n    outline: none;\n  }\n  </style>"],"sourceRoot":""}]);
